import React, { Component } from 'react';

const SINGLE_ITEM_QUERY = gql`
    query SINGLE_ITEM_QUERY($id: ID!) {
        item(where: { id: $id }) {
            id
            title
            description
            largeImage
        }
    }
`;

 class SingleItem extends Component {
  render() {
    return (
      <div>
        <p>This is the single item component</p>
      </div>
    )
  }
}

export default SingleItem;